import React, {useContext,useState} from 'react'
import { DataContext } from './DataProvider'

const Message = () => {

    const [checkAll, setCheckAll] = useState(false);
    const [todos,setTodos] = useContext(DataContext);

    const handleCheckAll = () => {
        const newTodos = [...todos];
        newTodos.forEach( todo => {
            todo.complete = !checkAll
        });
        setTodos(newTodos);
        setCheckAll(!checkAll);
    }

    const deleteTodo = () => {
        const newTodos = todos.filter ( todo => {
            return todo.complete ===false
        });
        setTodos(newTodos);
        setCheckAll(false);
    }
    
    return (
        <div className='Message text-white'>
            {
                todos.length === 0 ? <h3>You have no task left</h3> 
                    :   <div className='row_'> 
                            <label htmlFor='all' className=''>
                                <input type='checkbox' name='all'id='all' 
                                onClick={handleCheckAll} checked={checkAll}/>
                                ALL
                            </label>
                            <p>{todos.length < 2  ? <>You have {todos.length} task</> : <>You have {todos.length} tasks</>}</p>
                            <button id='delete' onClick={deleteTodo} className='btn btn-danger'>Delete</button>
                        </div>
            }
        </div>
       
    )
}

export default Message;
