
import '../App.css';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import FavoriteIcon from '@material-ui/icons/Favorite';
import PersonPinIcon from '@material-ui/icons/PersonPin';
import TodayIcon from '@material-ui/icons/Today';
import PropTypes from 'prop-types';
import {useState} from 'react';
import AppBar from '@material-ui/core/AppBar';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import { DataProvider } from './DataProvider';
import { ThoughtProvider } from './ThoughtProvider';
import Form from './Form';
import Form_ from './Form_';
import List from './List';
import List_ from './List_';
import Message from './Message';
import Message_ from './Message_';


const useStyles = makeStyles({
  root: {
    flexGrow: 1,
    maxWidth: '100%',
    minHeight: '100%',
  },
});

function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

function Main() {
  const classes = useStyles();
  const [value, setValue] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className='App-main bg-dark'>
      
      <AppBar position="static" className='bg-dark text-white'>
        <Tabs 
          value={value} 
          onChange={handleChange} 
          aria-label="simple tabs example"
          variant="fullWidth"
          indicatorColor="secondary"
          textColor="white"
          aria-label="icon label tabs example"
        >
       
          <Tab icon={<TodayIcon />} label="Jocyll's To-do List" {...a11yProps(0)} />
          <Tab icon={<FavoriteIcon />} label="Jocyll's Thoughts" {...a11yProps(1)} />  
        </Tabs>
      </AppBar>

      <TabPanel value={value} index={0}>
        <DataProvider>
          <div className='App-task p-5 shadow-lg'>
            <Form />
            <List />
            <Message />
          </div>
        </DataProvider>
      </TabPanel>
      <TabPanel value={value} index={1}>
        <ThoughtProvider>
            <div className='App-thought p-5 shadow-lg'>
              <Form_ />
              <List_ />
              <Message_ />
            </div>
        </ThoughtProvider>
      </TabPanel>
    </div>

  );
}

export default Main;
