import React, { useState,useEffect,createContext } from 'react'

export const DataContext = createContext();
// i think this lets you export then import data to/from other components

export const ThoughtProvider = (props) => {
    const [todos, setTodos] = useState([])

    useEffect(() => {
        const thoughtStore = JSON.parse(localStorage.getItem('thoughtStore'))
       if(thoughtStore) setTodos(thoughtStore)
    }, [])


    useEffect(() => {
        localStorage.setItem('thoughtStore',JSON.stringify(todos))
    }, [todos])
    
    
    return (
        <div>
            <DataContext.Provider value={[todos,setTodos]}>
                {props.children}
            </DataContext.Provider>
        </div>
    )
}
