import React, { useState,useContext } from 'react'
import { TextField } from '@material-ui/core';
import { DataContext} from './ThoughtProvider';


const Form_ = () => {
    
    const [todos, setTodos] = useContext(DataContext); 
    const [todoName, setTodoName] = useState(''); // useState for name of task and action of typing task on input
    const [todoDate, setTodoDate] = useState('');

    const addTodo = e => {
        e.preventDefault();
        setTodos([...todos, {name: todoName, date: todoDate, complete:false}]) // action to add todos plus new object todo in array 
        setTodoName(''); // clear input field after creating task
    }

    return (
        <div>
            <form autoComplete='off' onSubmit={addTodo}>
                <input name='date' value={todoDate} onChange={e => setTodoDate(e.target.value)} type='date' className='col my-3 form-control' style={{width:'200px', float:'right'}}/>
                <TextField 
                    type='text'
                    name='todos'
                    id='todos'
                    value={todoName}
                    onChange={e => setTodoName(e.target.value.toLowerCase())}
                    placeholder='Type here...'
                    color="secondary"
                    textColor="white"
                    multiline
                    rows={4}
                    required
                    fullWidth
                />
               
                <button type='submit' className='mt-2 btn btn-dark'>
                    Input thoughts
                </button>
            </form>
        </div>
        
    )
}

export default Form_
