import React, { useState }from 'react';


const ListItem = ({todo,id,checkComplete,handleEditTodos}) => {

    const [onEdit, setOnEdit] = useState(false)
    const [editValue, setEditValue] = useState(todo.name)

    const handleOnEdit = () => {
        setOnEdit(true)
    }

    const handleSave = id => {
        setOnEdit(false)
        if(editValue) {
            handleEditTodos(editValue,id)
        } else {
            setEditValue(todo.name)
        }
    }

    if(onEdit) {
      
        return (
            <div> 
                <li className='border-light'>
                    <textarea 
                        type='text' 
                        id='editValue' 
                        name='editValue'
                        value={editValue}
                        className='form-control'
                        onChange={e => setEditValue(e.target.value.toLowerCase())}
                    />
                    <button onClick={()=> handleSave(id)} className='ms-3 btn btn-success'> Save </button>
                </li>
            </div>
        )
    } else {
        return (
            <div> 
                    <li className='border-light text-white' >
                        <label htmlFor={id} className={todo.complete ? 'active':''}>
                            <input 
                                type='checkbox' 
                                id={id} 
                                checked={todo.complete} 
                                onChange={() => checkComplete(id)}
                            />
                            <span className="me-3">{todo.date}</span>
                            {todo.name} 
                        </label>
                        <button disabled={todo.complete} onClick={handleOnEdit} className='ms-3 btn btn-warning'> Edit </button>
                    </li>     
            </div>
        )  
    }
}

export default ListItem;
